body{
    background-color: #383825bb;
  }
  .App{
    background-color: #150b2e;
    min-height:500px;
    width: 400px;
    margin: 40px auto;
  }
  
  #to-do-form input{
    background-color: rgba(15, 151, 241, 0.616);
    border: 0; 
    color: rgb(247, 248, 241);
    width: 200px;
    height: 50px;
    margin: 20px;
    padding: 0 20px;
    font-size: 18px;
    border-radius: 10px;
  }
  #to-do-form input::placeholder{
    color: rgba(255, 255, 255, 0.5);
  }
  #to-do-form input:focus{
    outline: none;
  }
  #to-do-form button{
    height: 50px;
    width: 80px;
    border-radius: 20px;
    border: 0;
    background-color:rgba(202, 156, 24, 1); 
    cursor: pointer;
  }
  #to-do-form button:focus{
    outline: none;
  }
  